import React, { useRef } from "react";
import "./Login.css";

function Login() {
  const modelRef = useRef();

  const inputFormStyle = {
    marginBottom: "20px",
    width: "90%",
    fontSize: "0.8rem",
  };

  const loginBtnStyle = {
    marginBottom: "15px",
    width: "90%",
  };

  const loginBtnStyle2 = {
    marginBottom: "20px",
    width: "75%",
    fontSize: "0.8rem",
  };

  return (
    <div className="container">
      <div className="text">
        <h1>facebook</h1>
        <p>Connect with friends and the world around you on Facebook.</p>
      </div>

      <div className="form">
        <form>
          <input
            style={inputFormStyle}
            type="email"
            id="email"
            className="nes-input"
            placeholder="Email or phone number"
          />
          <input
            style={inputFormStyle}
            type="password"
            id="password"
            className="nes-input"
            placeholder="Password"
          />
          <button
            style={loginBtnStyle}
            type="button"
            className="nes-btn is-primary"
          >
            Login
          </button>
          <p
            style={{ marginBottom: "20px" }}
            className="forgot__password nes-pointer"
          >
            Forgot Password ?
          </p>
          <hr />

          <button
            onClick={() => {
              modelRef.current.showModal();
            }}
            style={loginBtnStyle2}
            type="button"
            className="nes-btn is-success"
          >
            Create New Account
          </button>
        </form>
        <dialog
          className="nes-dialog is-rounded signup__modal"
          id="dialog-rounded"
          ref={modelRef}
        >
          <span className="crossBtn nes-pointer" onClick={() =>{
            modelRef.current.removeAttribute('open');
            window.location.assign(window.location)
          } }>X</span>
          <div className="title">
            <p>
              Sign Up <br />
              <span>Itâ€™s quick and easy.</span>
            </p>
          </div>
          <div className="inputs">
            <div className="names">
              <input
                type="text"
                id="firstname"
                className="nes-input"
                placeholder="First name"
              />
              <input
                type="text"
                id="lastname"
                className="nes-input"
                placeholder="Last name"
              />
            </div>
            <div className="email">
              <input
                type="email"
                id="email"
                className="nes-input"
                placeholder="Email"
              />
            </div>
            <div className="password">
              <input
                type="password"
                id="password"
                className="nes-input"
                placeholder="Password"
              />
            </div>
            <div className="gender">
              <p>Gender</p>
              <label>
                <input type="radio" className="nes-radio" name="answer" checked />
                <span>Male</span>
              </label>

              <label>
                <input type="radio" className="nes-radio" name="answer" />
                <span>Female</span>
              </label>
              <label>
                <input type="radio" className="nes-radio" name="answer" />
                <span>Custom</span>
              </label>
            </div>
            <div className="terms">
            By clicking Sign Up, you agree to our Terms, Data Policy and Cookies Policy. You may receive SMS Notifications from us and can opt out any time.
            </div>
            <div className="signupBtn">
            <button
            style={{width: '200px'}}
            type="button"
            className="nes-btn is-success"
          >
           Sign Up
          </button>
            </div>
          </div>
        </dialog>
      </div>
    </div>
  );
}

export default Login;
